table {
    font-family: lucida sans unicode,lucida grande,Sans-Serif;
    font-size: 14px;
    width:75%;
    text-align: left;
    float:left;
    margin-left: 70px;
    margin-top: 30px;
    margin-bottom: 50px;
}

.update-form{
    display: flex;
    flex-direction: column;
}

table th {
    font-size: 18px;
    font-weight: 400;
    border-bottom: 2px solid #6678b1;
    padding: 10px 8px;
}

table td {
    border-bottom: 1px solid #ccc;
    padding: 13px 14px;
}

