.App {
    font-family: sans-serif;
    text-align: center;
}

* {
    box-sizing: "border-box";
}

html,
body {
    padding: 0;
    margin: 0;
}

input {
    padding: 8px;
    margin: 2px;
    border-radius: 2px;
    border: #f2f2f2 solid 1px;
}

nav {
    font-size: large;
    background-color: #268bc6;
    background: linear-gradient(#004e92 , #268bc6);
}

nav a {
    color: #fff;
    text-decoration: none;
    padding: 10px;
    display: inline-block;
}

nav a:hover {
    color: #2da2db;
}

nav a.active {
    padding-bottom: 8px;
    border-bottom: 2px solid #fff;
}

.addAuthor {
    background-color: lightgrey;
    padding: 20px;
    font-weight: bold;
}

div.author {
    margin: 0;
    padding: 0;
    background-color: #2da2db;
    border-bottom: 1px #f2f2f2 solid;
}

div.author .info {
    padding: 10px;
}

.author-expand {
    display: none;
    background-color: #004e92;
    padding: 10px;
    width: auto;
}

.author-expand.show {
    display: block;
}

.btn {
    display: inline-block;
    margin-bottom: 0;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 1rem;
    border-radius: 3px;
    border: 1px solid transparent;
    text-decoration: none;
    user-select: none;
    margin: 5px 5px;

    color: #eceff1;
    border-color: transparent;
    background: #78909c;
    box-shadow: none;
}
.btn:focus,
.btn:active:focus,
.btn.active:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
}
.btn:hover,
.btn:focus,
.btn.focus {
    border-color: transparent;
    color: #fff;
    background: #90a4ae;
    box-shadow: none;
}
.btn:active,
.btn.active {
    border-color: #546e7a;
    color: #cfd8dc;
    background: #546e7a;
    box-shadow: none;
}
.btn.disabled,
.btn[disabled],
fieldset[disabled] .btn {
    background: #b0bec5;
    border-color: #b0bec5;
}

