.home-h1 {
    text-align: center;
    font-size: 50px;
    text-indent: 10px;
    margin: auto;
    padding: 2rem;
    color: black;
    background-color: white;
    margin-top: 4rem ;
}

.home-header {
    width: auto;
    display: block;
    min-width: 500px;
    margin: auto;
}

h2 {
    padding: 10px;
    text-align: center;
    margin-top: 1%;
}

document.body {
    background-color: green;
}


body.home-body {
    background-color: white;
    color: black;
}

.home-container {
    margin-top: 5%;
}

.home-form {
    max-width: 350px;
    margin: auto;
    position: center;
}


.home-card {
    display: block;
    width: 85%;
    margin: 0 0 20px 0;
    padding: 10px 5px 20px 5px;
    background-color: #f4f4f4;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, .5);
    min-width: 500px;
}


h5 {
    text-align: center;
    font-weight: normal;
    padding: 2rem;
    margin: auto;
    display: block
}

.receipt-logo {
    padding: 0px 10px 10px;
    margin: auto;
    display: block;
    width: 40%;
}

/*@media (prefers-reduced-motion: no-preference) {
    .receipt-logo {
        animation: receipt-logo-spin infinite 20s linear;
    }
}*/

@keyframes receipt-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}


.home-button {
    display: flex;
    margin: 0 auto;
    align-self: center;
}

.home-footer {
    background-color: #282c34;
    color: white;
    text-align: center;
    display: block;
    width: 100%;
}

